<template>
  <div class="McMap">
    <div id="main"></div>
  </div>
</template>

<script>
import MCdata from '@/assets/MCdata.json'
export default {
  data(){return{}},
  mounted(){
    var myChart=this.$echarts.init(document.getElementById('main'));
    /**************************
    【 配置区 】 :
    *************************
    配置游戏区正方体边长（砖块数）*/
    var xLength = 20 ;
    /* 配置上次导出（save）的游戏数据，没有请填空字符串 */
    //let loadData = '' ;
    let loadData=MCdata;

    /* 数据是否自动导入 */
    let autoLoad = true ;

    /* 菜单配置（颜色按钮、尺寸按钮） */
    let menuConfig = {
      colorList: ['green', 'yellow', 'SaddleBrown','DarkGray','blue'],
      sizeList: [0.25, 0.5, 1, 2, 3]
    };


    /*
    *************************
    【 generateData 函数说明 】 :
    *************************
        根据传入的边长，生成游戏数据————支撑一个正方体的游戏区域。
    生成的数据包括：
    {
        x: ['x_1', 'x_2', ..., 'x_n'],
        y: ['y_1', 'y_2', ..., 'y_n'],
        boxWidth: length,
        boxDepth: length,
        boxHeight: length,
        operatingSeriesData: [[0, 0, 1], [0, 1, 1], ..., [n, n, 1]],
        brickSeriesData: [[{value:[0, 0, 0]}, {value:[0, 1, 0]}, ..., {value:[n, n, 0]}],
                        [{value:[0, 0, 0]}, {value:[0, 1, 0]}, ..., {value:[n, n, 0]}], ... ]
    }

    其中：
        1、x、y 用于 xAxis3D、yAxis3D 的 data；
        2、boxWidth、boxDepth、boxHeight 用于 grid3D 的相应参数；
        3、operatingSeriesData 用于 bar3D，实现堆砖块（积木）的操作层，监听鼠标点击
            事件 myChart.on('click', params => {})
        4、brickSeriesData 同样用于 bar3D，实现堆积完成的砖块（积木）


        注: 游戏数据可以通过 heatmap 实现的按钮导出，下次赋值给 loadData 变量，实现
            数据的载入，从而继续游戏。

    *************************
    【 generateMenuData 函数说明 】 :
    *************************
        根据传入的颜色列表、尺寸列表（砖块高度），生成菜单数据（heatmap 的 data），
    用于实现右上角的功能菜单。

        注: 第一行是操作菜单，第二行是尺寸菜单，第三行是颜色菜单。

    *************************
    【 generateSeries 函数说明 】 :
    *************************
        根据传入的数据，生成 option.series 用于渲染游戏数据————bar3D

        注: 可以传入 generateData 生成的 data，也可以载入 loadData，后面用了一个
            三元表达式做了判断，如果 autoLoad 为真，且 loadData 非空字符串，才
            执行 generateData 函数。

    *************************
    【 undo、redo 函数说明 】 :
    *************************
        【undoList/redoList 记录格式】
        {
            seriesIndex: i,
            dataIndex: params.data[0] * xLength + params.data[1],
            brickConfig: JSON.parse(JSON.stringify(brickConfig))
        }

        【undo】
        撤销上一次操作

        注1: 使用 history.undoList 记录操作历史，undo 就是将 data[i]，
            还原成{value: [i, j, 0]}
        注2: 还原的同时，将对应 history.undoList 记录删除，存入 history.redoList


        【redo】
        重做上一次撤销

        注1: 使用 history.redoList 记录操作历史，redo 就是将 data[i]，
            重新修改成 data[i].value[2] > 0 的，包含 itemStyle 的过程。
        注2: 重做的同时，将对应 history.redoList 记录删除，重新存入 history.undoList

    *************************/

    let generateData = (length) => {
      let ret = {
        x: [],
        y: [],
        boxWidth: length,
        boxDepth: length,
        boxHeight: length,
        operatingSeriesData: [],
        brickSeriesData: []
      };

      let brickSeriesDataItem = [];

      for (let i = 0; i < length; i++) {
        ret.x.push('x_' + i);
        ret.y.push('y_' + i);

        for (let j = 0; j < length; j++) {
          ret.operatingSeriesData.push([i, j, 1]);
          brickSeriesDataItem.push({
            value: [i, j, 0]
          });
        }

      }

      for (let i = 0; i < length; i++) {
        ret.brickSeriesData[i] = JSON.parse(JSON.stringify(brickSeriesDataItem));
      }

      return ret;
    };

    let generateMenuData = (colorList, sizeList) => {

      let ret = [];


      for (let i = 0; i < sizeList.length; i++) {

        ret.push({
          value: [i, 1, sizeList[i]],
          name: 'size',
          label: {
            show: true,
            color: 'black'
          },
          itemStyle: {
            color: 'gray'
          }
        });
      }

      for (let i = 0; i < colorList.length + 1; i++) {

        if (i === colorList.length) {

          ret.push({
            value: [i, 0, 1],
            name: 'empty',
            label: {
              show: true,
              color: 'black'
            },
            itemStyle: {
              color: '#FFF',
              opacity: 0.1
            }
          });
          continue;
        }

        ret.push({
          value: [i, 0, 1],
          name: 'color',
          label: {
            show: true,
            color: 'black'
          },
          itemStyle: {
            color: colorList[i]
          }
        });
      }

      ret.push({
        value: [0, 2, 1],
        name: 'undo',
        label: {
          show: true,
          color: 'black'
        },
        itemStyle: {
          color: 'gray'
        }
      }, {
        value: [1, 2, 1],
        name: 'redo',
        label: {
          show: true,
          color: 'black'
        },
        itemStyle: {
          color: 'gray'
        }
      }, {
        value: [2, 2, 1],
        name: 'reset',
        label: {
          show: true,
          color: 'black'
        },
        itemStyle: {
          color: 'gray'
        }
      }, {
        value: [3, 2, 1],
        name: 'save',
        label: {
          show: true,
          color: 'black'
        },
        itemStyle: {
          color: 'gray'
        }
      }, {
        value: [4, 2, 1],
        name: 'load',
        label: {
          show: true,
          color: 'black'
        },
        itemStyle: {
          color: 'gray'
        }
      });

      return ret;
    };

    let generateSeries = (src) => {
      let  ret = [];

      for (let i = 0; i < src.boxHeight; i++) {

        ret.push({
          type: 'bar3D',
          name: 'bricks',
          color: 'LawnGreen',
          data: src.brickSeriesData[i],
          bevelSize: i === 0 ? 0 : 0.2,
          bevelSmoothness: i === 0 ? 0 : 2,
          barSize: [1, 1],
          stack: 'stack',
          silent: true,
          shading: 'lambert',
          itemStyle: {
            opacity: i === 0? 1: 0
          }
        });


      }
      ret.push({
        type: 'bar3D',
        name: 'operatingSeries',
        data: src.operatingSeriesData,
        barSize: [1, 1],
        stack: 'stack',
        color: '#FFA',
        shading: 'lambert',
        label: {
          emphasis: {
            show: false
          }

        },
        itemStyle: {
          opacity: 0.01
        },
        emphasis: {
          itemStyle: {
            opacity: 1
          }
        }
      });

      ret.push({
        type: 'heatmap',
        name: 'menu',
        tooltip: {
          formatter: params => {
            if (params.name === 'color') {
              return `点击更换“积木”颜色为 ${params.color}`;
            }

            if (params.name === 'size') {
              return `点击更换“积木”高度为 ${params.value[2]}`;
            }

            return {undo: '撤销', redo: '重做', reset: '清空', save: '导出游戏数据，<br />供下次赋值给 loadData 使用', load: '功能开发中…' }[params.name];

          }
        },
        label: {
          normal:{
            formatter: params => {

              if (params.name === 'color') {
                return params.color;
              }

              if (params.name === 'size') {
                return params.value[2];
              }

              return params.name;

            }
          }
        },
        itemStyle: {
          borderColor: '#AAA',
          borderWidth: 4
        },
        data: generateMenuData(menuConfig.colorList, menuConfig.sizeList)
      });


      return ret;
    };

// 撤销
    let undo = () => {
      if (history.undoList.length === 0) {
        alert('操作历史记录为空，撤销未执行…');
        return console.log('操作历史记录为空，撤销未执行…');
      }

      // undoList 最后一条记录“剪切”到 redoList
      let historyObj = history.undoList.pop();
      history.redoList.push(historyObj);

      // 将上一步操作/重做的 series[seriesIndex].data[dataIndex] 重置为初始值
      let val = series[historyObj.seriesIndex].data[historyObj.dataIndex].value;
      val[2] = 0;
      series[historyObj.seriesIndex].data[historyObj.dataIndex] = {value: val};

      myChart.setOption({series: series});
      console.log('撤销成功');
    };

// 重做
    let redo = () => {
      if (history.redoList.length === 0) {
        alert('操作历史记录为空，重做未执行…');
        return console.log('操作历史记录为空，重做未执行…');
      }

      // redoList 最后一条记录“剪切”到 undoList
      let historyObj = history.redoList.pop();
      history.undoList.push(historyObj);

      // 将上一步重置的 series[seriesIndex].data[dataIndex] 重设为撤销前的状态
      series[historyObj.seriesIndex].data[historyObj.dataIndex].value[2] = historyObj.brickConfig.size;
      series[historyObj.seriesIndex].data[historyObj.dataIndex].itemStyle = {
        color: historyObj.brickConfig.color,
        opacity: historyObj.brickConfig.opacity
      };
      myChart.setOption({series: series});
      console.log('重做成功');
    };

// 撤销/重做 所用的操作历史记录
    let history = {
      undoList: [],
      redoList: []
    };

// 下一个砖块的样式
    let brickConfig = {
      color: menuConfig.colorList[0],
      size: menuConfig.sizeList[0],
      opacity: 1
    };

// 如设置自动载入，而且用于载入的数据非空（空字符串），那么不再重新生成新数据
    var data = autoLoad && loadData !== '' ? loadData : generateData(xLength);

// 通过数据生成 series 配置
    let series = generateSeries(data);

// 配置 option
    let option = {
      title: {
        subtext: `当前颜色：${brickConfig.color}\n当前尺寸：${brickConfig.size}\n当前透明度：${brickConfig.opacity}`,
        left: '70%',
        top: '22%'
      },
      tooltip: {
        trigger: 'item'
      },
      toolbox: {
        feature: {
          dataView: {},
          saveAsImage: {}
        }
      },
      xAxis3D: {
        type: 'category',
        show: false,
        axisLabel: {
          show: false
        },
        axisTick: {
          show: false
        },
        splitLine: {
          show: false
        },
        axisPointer: {
          show: false
        },
        data: data.x
      },
      yAxis3D: {
        type: 'category',
        show: false,
        axisLabel: {
          show: false
        },
        axisTick: {
          show: false
        },
        splitLine: {
          show: false
        },
        axisPointer: {
          show: false
        },
        data: data.y
      },
      zAxis3D: {
        show: false,
        max: data.boxHeight,
        axisLabel: {
          show: false
        },
        axisTick: {
          show: false
        },
        splitLine: {
          show: false
        },
        axisPointer: {
          show: false
        },
        type: 'value'
      },
      grid3D: {
        show: false,
        boxWidth: data.boxWidth,
        boxDepth: data.boxDepth,
        boxHeight: data.boxHeight,
        light: {
          main: {
            alpha: 60,
            beta: 210,
            shadow: true
          }
        },
        viewControl: {
          alpha: 0,
          beta: 210,
          distance: 40,
          minDistance: 0
        },

      },
      grid: {
        top: '10%',
        left: '70%',
        bottom: '80%'
      },
      xAxis: {
        show:false,
        type: 'category'
      },
      yAxis: {
        show: false,
      },
      series: series,
    };

// 一个 utf8_to_b64
    let base64 = (s) => {
      return window.btoa(unescape(encodeURIComponent(s)));
    };

//myChart.setOption(option);

// 监听鼠标点击事件
    myChart.on('click', params => {

      // 菜单操作处理
      if (params.seriesName === 'menu') {
        if (params.name === 'color') {
          brickConfig.color = params.color;
          brickConfig.opacity = 1;
          myChart.setOption({title:{subtext: `当前颜色：${brickConfig.color}\n当前尺寸：${brickConfig.size}\n当前透明度：${brickConfig.opacity}`}});
          return console.log(`砖块颜色更换为${params.color}`);
        }

        if (params.name === 'empty') {
          brickConfig.color = params.color;
          brickConfig.opacity = 0;
          myChart.setOption({title:{subtext: `当前颜色：${brickConfig.color}\n当前尺寸：${brickConfig.size}\n当前透明度：${brickConfig.opacity}`}});
          return console.log(`砖块颜色更换为透明`);
        }

        if (params.name === 'size') {
          brickConfig.size = params.value[2];
          myChart.setOption({title:{subtext: `当前颜色：${brickConfig.color}\n当前尺寸：${brickConfig.size}\n当前透明度：${brickConfig.opacity}`}});
          return console.log(`砖块 size 更换为${params.value[2]}`);
        }

        if (params.name === 'load') {
          // load
          alert('开发中…');
          return console.log('开发中…');
        }

        if (params.name === 'reset') {
          data = generateData(xLength);
          series = generateSeries(data);
          myChart.setOption({series: series});
          return console.log('清空数据成功');
        }


        if (params.name === 'save') {
          let uri = 'data:application/json;base64,';
          //console.log(data);
          window.location.href = uri + base64(JSON.stringify(data));
          return console.log('导出数据成功');
        }

        if (params.name === 'undo') {
          return undo();
        }

        if (params.name === 'redo') {
          return redo();
        }
      }

      //alert(`正在 (${params.data[0]}, ${params.data[1]}) 处堆积一个砖块`);

      // 堆积木（砖块）操作处理
      for (let i in series) {
        if (series[i].name === 'bricks' && series[i].data[params.data[0] * xLength + params.data[1]].value[2] === 0) {
          series[i].data[params.data[0] * xLength + params.data[1]].value[2] = brickConfig.size;
          series[i].data[params.data[0] * xLength + params.data[1]].itemStyle = {
            color: brickConfig.color,
            opacity: brickConfig.opacity
          };

          history.undoList.push({
            seriesIndex: i,
            dataIndex: params.data[0] * xLength + params.data[1],
            brickConfig: JSON.parse(JSON.stringify(brickConfig)) // 深拷贝
          });

          history.redoList = [];

          return myChart.setOption({
            series: series
          });

        }
      }
    });
    myChart.setOption(option);
  },
}
</script>

<style scoped lang="scss">
.McMap{
  width: 100%;
  #main{
    width: 100%;
    height: 500px;
  }
}
</style>